import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProductStockComponent } from './product-stock.component';

import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from 'src/app/shared/shared.module';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { ProductStockService } from 'src/app/service/product-stock.service';

export const routes: Routes = [
  {
    path: '',
    component: ProductStockComponent
  }
]
@NgModule({
  declarations: [ProductStockComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    SearchModule,
    SharedModule,
    PaginationModule
  ], providers: [ProductStockService]
})
export class ProductStockModule { }
